import { RSAA } from "redux-api-middleware";
import { API_ROOT } from "Core/constants";
import {
  GET_EXHIBITION_LIST_REQUEST,
  GET_EXHIBITION_LIST_SUCCESS,
  GET_EXHIBITION_LIST_FAILURE,
  GET_EXHIBITION_DETAIL_REQUEST,
  GET_EXHIBITION_DETAIL_SUCCESS,
  GET_EXHIBITION_DETAIL_FAILURE,
  GET_ENROLLED_EXHIBITION_LIST_REQUEST,
  GET_ENROLLED_EXHIBITION_LIST_SUCCESS,
  GET_ENROLLED_EXHIBITION_LIST_FAILURE,
  GET_EXHIBITION_SUBSCRIPTION_OVERVIEW_REQUEST,
  GET_EXHIBITION_SUBSCRIPTION_OVERVIEW_SUCCESS,
  GET_EXHIBITION_SUBSCRIPTION_OVERVIEW_FAILURE,
  EXHIBITION_SUBSCRIPTION_REQUEST,
  EXHIBITION_SUBSCRIPTION_SUCCESS,
  EXHIBITION_SUBSCRIPTION_FAILURE,
  SET_SELECTED_SUBSCRIPTION_OPTION
} from "./constants";

export function getExhibitionAction() {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/exhibition/list`,
      method: "GET",
      types: [
        GET_EXHIBITION_LIST_REQUEST,
        GET_EXHIBITION_LIST_SUCCESS,
        GET_EXHIBITION_LIST_FAILURE
      ]
    }
  };
}

export function getEnrolledExhibitionAction() {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/exhibition/subscribed/list`,
      method: "GET",
      types: [
        GET_ENROLLED_EXHIBITION_LIST_REQUEST,
        GET_ENROLLED_EXHIBITION_LIST_SUCCESS,
        GET_ENROLLED_EXHIBITION_LIST_FAILURE
      ]
    }
  };
}

export function getExhibitionDetailAction(id) {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/exhibition/${id}/view`,
      method: "GET",
      types: [
        GET_EXHIBITION_DETAIL_REQUEST,
        GET_EXHIBITION_DETAIL_SUCCESS,
        GET_EXHIBITION_DETAIL_FAILURE
      ]
    }
  };
}

export function getExhibitionSubscriptionOverview(id) {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/exhibition/subscribe/${id}/overview`,
      method: "GET",
      types: [
        GET_EXHIBITION_SUBSCRIPTION_OVERVIEW_REQUEST,
        GET_EXHIBITION_SUBSCRIPTION_OVERVIEW_SUCCESS,
        GET_EXHIBITION_SUBSCRIPTION_OVERVIEW_FAILURE
      ]
    }
  };
}

export function subscribeToExhibition(id, typeObject) {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/exhibition/subscribe/${id}`,
      method: "POST",
      body: JSON.stringify(typeObject),
      types: [
        EXHIBITION_SUBSCRIPTION_REQUEST,
        EXHIBITION_SUBSCRIPTION_SUCCESS,
        EXHIBITION_SUBSCRIPTION_FAILURE
      ]
    }
  };
}

export function setSelectedSubscriptionOption(selectedSubscriptionOption) {
  return {
    type: SET_SELECTED_SUBSCRIPTION_OPTION,
    payload: selectedSubscriptionOption
  };
}
