import {
  GET_MARKETPLACE_PROFILE_DETAILS_REQUEST,
  GET_MARKETPLACE_PROFILE_DETAILS_SUCCESS,
  GET_MARKETPLACE_PROFILE_DETAILS_FAILURE,
  EDIT_MARKETPLACE_PROFILE_DETAILS_REQUEST,
  EDIT_MARKETPLACE_PROFILE_DETAILS_SUCCESS,
  EDIT_MARKETPLACE_PROFILE_DETAILS_FAILURE
} from "./constants";

const initialState = {
  profile: {}
};

export default function marketplaceProfileReducer(
  state = initialState,
  { type, payload }
) {
  switch (type) {
    case GET_MARKETPLACE_PROFILE_DETAILS_REQUEST:
      return {
        ...state
      };
    case GET_MARKETPLACE_PROFILE_DETAILS_SUCCESS:
      return {
        ...state,
        profile: payload.data.profile
      };
    case GET_MARKETPLACE_PROFILE_DETAILS_FAILURE:
      return {
        ...state
      };
    case EDIT_MARKETPLACE_PROFILE_DETAILS_REQUEST:
      return {
        ...state
      };
    case EDIT_MARKETPLACE_PROFILE_DETAILS_SUCCESS:
      return {
        ...state
      };
    case EDIT_MARKETPLACE_PROFILE_DETAILS_FAILURE:
      return {
        ...state
      };
    default:
      return state;
  }
}
