import { RSAA } from "redux-api-middleware";
import { API_ROOT } from "Core/constants";
import {
  GET_MARKETPLACE_PROFILE_DETAILS_REQUEST,
  GET_MARKETPLACE_PROFILE_DETAILS_SUCCESS,
  GET_MARKETPLACE_PROFILE_DETAILS_FAILURE,
  EDIT_MARKETPLACE_PROFILE_DETAILS_REQUEST,
  EDIT_MARKETPLACE_PROFILE_DETAILS_SUCCESS,
  EDIT_MARKETPLACE_PROFILE_DETAILS_FAILURE
} from "./constants";

export function getMarketplaceProfileAction() {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/marketplace/profile`,
      method: "GET",
      body: JSON.stringify(),
      types: [
        GET_MARKETPLACE_PROFILE_DETAILS_REQUEST,
        GET_MARKETPLACE_PROFILE_DETAILS_SUCCESS,
        GET_MARKETPLACE_PROFILE_DETAILS_FAILURE
      ]
    }
  };
}

export function editMarketplaceProfileAction(updatedMarketplaceProfileObject) {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/marketplace/profile`,
      method: "POST",
      body: JSON.stringify(updatedMarketplaceProfileObject),
      types: [
        EDIT_MARKETPLACE_PROFILE_DETAILS_REQUEST,
        EDIT_MARKETPLACE_PROFILE_DETAILS_SUCCESS,
        EDIT_MARKETPLACE_PROFILE_DETAILS_FAILURE
      ]
    }
  };
}
