import { RSAA } from "redux-api-middleware";
import { API_ROOT } from "Core/constants";
import {
  GET_SUBSCRIPTION_LIST_REQUEST,
  GET_SUBSCRIPTION_LIST_SUCCESS,
  GET_SUBSCRIPTION_LIST_FAILURE,
  SET_SELECTED_SUBSCRIPTION,

  GET_ACTIVE_SUBSCRIPTION_REQUEST,
  GET_ACTIVE_SUBSCRIPTION_SUCCESS,
  GET_ACTIVE_SUBSCRIPTION_FAILURE,

  GET_FEATURE_USAGE_REQUEST,
  GET_FEATURE_USAGE_SUCCESS,
  GET_FEATURE_USAGE_FAILURE,
} from "./constants";

export function getPlanListAction() {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/plans`,
      method: "GET",
      types: [
        GET_SUBSCRIPTION_LIST_REQUEST,
        GET_SUBSCRIPTION_LIST_SUCCESS,
        GET_SUBSCRIPTION_LIST_FAILURE
      ]
    }
  };
}

export function getActivePlan() {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/plan`,
      method: "GET",
      types: [
        GET_ACTIVE_SUBSCRIPTION_REQUEST,
        GET_ACTIVE_SUBSCRIPTION_SUCCESS,
        GET_ACTIVE_SUBSCRIPTION_FAILURE,
      ]
    }
  };
}

export function getFeatureUsage(slug) {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/usage`,
      method: "POST",
      body: JSON.stringify({ slug }),
      types: [
        GET_FEATURE_USAGE_REQUEST,
        GET_FEATURE_USAGE_SUCCESS,
        GET_FEATURE_USAGE_FAILURE,
      ]
    }
  };
}


export function setSelectedSubscription(selectedSubscription) {
  return {
    type: SET_SELECTED_SUBSCRIPTION,
    payload: selectedSubscription,
  }
}
