import {
  GET_EXHIBITION_LIST_REQUEST,
  GET_EXHIBITION_LIST_SUCCESS,
  GET_EXHIBITION_LIST_FAILURE,
  GET_EXHIBITION_DETAIL_REQUEST,
  GET_EXHIBITION_DETAIL_SUCCESS,
  GET_EXHIBITION_DETAIL_FAILURE,
  GET_ENROLLED_EXHIBITION_LIST_REQUEST,
  GET_ENROLLED_EXHIBITION_LIST_SUCCESS,
  GET_ENROLLED_EXHIBITION_LIST_FAILURE,
  GET_EXHIBITION_SUBSCRIPTION_OVERVIEW_REQUEST,
  GET_EXHIBITION_SUBSCRIPTION_OVERVIEW_SUCCESS,
  GET_EXHIBITION_SUBSCRIPTION_OVERVIEW_FAILURE,
  EXHIBITION_SUBSCRIPTION_REQUEST,
  EXHIBITION_SUBSCRIPTION_SUCCESS,
  EXHIBITION_SUBSCRIPTION_FAILURE,
  SET_SELECTED_SUBSCRIPTION_OPTION
} from "./constants";

const initialState = {
  exhibitionList: {},
  subscribedExhibitionList: {},
  exhibitionDetail: {},
  subscriptionOverview: {},
  selectedSubscriptionOption: {}
};

export default function exhibitionReducer(
  state = initialState,
  { type, payload }
) {
  switch (type) {
    case GET_EXHIBITION_LIST_REQUEST:
      return {
        ...state
      };
    case GET_EXHIBITION_LIST_SUCCESS:
      return {
        ...state,
        exhibitionList: payload.data.exhibitions
      };
    case GET_EXHIBITION_LIST_FAILURE:
      return {
        ...state
      };
    case GET_EXHIBITION_DETAIL_REQUEST:
      return {
        ...state
      };
    case GET_EXHIBITION_DETAIL_SUCCESS:
      return {
        ...state,
        exhibitionDetail: payload.data.exhibition
      };
    case GET_EXHIBITION_DETAIL_FAILURE:
      return {
        ...state
      };

    case GET_ENROLLED_EXHIBITION_LIST_REQUEST:
      return {
        ...state
      };
    case GET_ENROLLED_EXHIBITION_LIST_SUCCESS:
      return {
        ...state,
        subscribedExhibitionList: payload.data.exhibitions
      };
    case GET_ENROLLED_EXHIBITION_LIST_FAILURE:
      return {
        ...state
      };
    case GET_EXHIBITION_SUBSCRIPTION_OVERVIEW_REQUEST:
      return {
        ...state
      };
    case GET_EXHIBITION_SUBSCRIPTION_OVERVIEW_SUCCESS:
      return {
        ...state,
        subscriptionOverview: payload.data.exhibition
      };
    case GET_EXHIBITION_SUBSCRIPTION_OVERVIEW_FAILURE:
      return {
        ...state
      };
    case SET_SELECTED_SUBSCRIPTION_OPTION:
      return {
        ...state,
        selectedSubscriptionOption: payload
      };
    case EXHIBITION_SUBSCRIPTION_REQUEST:
      return {
        ...state
      };
    case EXHIBITION_SUBSCRIPTION_SUCCESS:
      return {
        ...state,
        payload
      };
    case EXHIBITION_SUBSCRIPTION_FAILURE:
      return {
        ...state
      };

    default:
      return state;
  }
}
