import {
  GET_ORDER_LIST_REQUEST,
  GET_ORDER_LIST_SUCCESS,
  GET_ORDER_LIST_FAILURE,
  GET_ORDER_DETAILS_REQUEST,
  GET_ORDER_DETAILS_SUCCESS,
  GET_ORDER_DETAILS_FAILURE
} from "./constants";

const initialState = {
  orderList: [],
  order: {}
};

export default function orderReducer(state = initialState, { type, payload }) {
  switch (type) {
    case GET_ORDER_LIST_REQUEST:
      return {
        ...state
      };
    case GET_ORDER_LIST_SUCCESS:
      return {
        ...state,
        orderList: payload.data.orders
      };
    case GET_ORDER_LIST_FAILURE:
      return {
        ...state
      };
    case GET_ORDER_DETAILS_REQUEST:
      return {
        ...state
      };
    case GET_ORDER_DETAILS_SUCCESS:
      return {
        ...state,
        order: payload.data
      };
    case GET_ORDER_DETAILS_FAILURE:
      return {
        ...state
      };

    default:
      return state;
  }
}
